<template>
  <div class="card strpied-tabled-with-hover">
    <el-table :data="dataSouce" border class="table-hover table-striped">
      <el-table-column prop="捷運站代號" label="捷運站代號" :formatter="formatter"></el-table-column>
      <el-table-column prop="站名" label="站名" :formatter="formatter"></el-table-column>
      <el-table-column prop="經度" label="經度" :formatter="formatter"></el-table-column>
      <el-table-column prop="緯度" label="緯度" :formatter="formatter"></el-table-column>
    </el-table>
  </div>
</template>


<style>
.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  background: #f0f9eb;
}
</style>
<script>
import TaxiInfoAPIService from 'src/service/MRTInfoAPIService.js'
export default {
  data() {
    return {
      dataSouce: []
    }
  },
  mounted() {
    this.dataBind()
  },
  methods: {
    dataBind() {
      TaxiInfoAPIService.get().then(response => {
        console.log(response.data)
        this.dataSouce = response.data
      })
    },
    formatter(row, column, cellValue, index) {
      return cellValue
    }
  }
}
</script>