
<template>
  <div class="card strpied-tabled-with-hover">
    <el-table :data="dataSouce" border class="table-hover table-striped">
      <el-table-column prop="tel" label="電話" :formatter="formatter"></el-table-column>
      <el-table-column prop="name" label="車隊名稱" :formatter="formatter"></el-table-column>
    </el-table>
  </div>
</template>



<style>
.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  background: #f0f9eb;
}
</style>
<script>
import TaxiInfoAPIService from 'src/service/TaxiInfoAPIService.js'
export default {
  data() {
    return {
      dataSouce: []
    }
  },
  mounted() {
    this.dataBind()
  },
  methods: {
    dataBind() {
      TaxiInfoAPIService.get().then(response => {
        this.dataSouce = response.data
      })
    },
    formatter(row, column, cellValue, index) {
      return cellValue
    }
  }
}
</script>